@charset "UTF-8";
html {
  box-sizing: border-box;
}

*,
::before,
::after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  background-color: #DCC6C6;
  text-align: center;
  width: 75%;
  margin-left: auto;
  margin-right: auto;
  height: auto;
}

main {
  background-color: #F0F0F0;
  padding: 30px;
}

@media screen and (max-width: 1100px) {
  body {
    width: 100%;
  }
  .page {
    display: flex;
    flex-direction: row;
    width: 100%;
    border: #1d1b1b 2px dashed;
    padding: 30px;
    margin: 10px;
  }
  .pageimage {
    width: 100%;
    display: block;
  }
}
@media screen and (min-width: 1100px) {
  .page {
    display: flex;
    flex-direction: column;
    width: 100%;
    border: #6B6565 2px dashed;
    padding: 30px;
    margin: 10px;
  }
  .pageimage {
    width: 100%;
  }
}
.header {
  margin: 50px;
  font-weight: 800;
  font-style: italic;
  font-size: 30px;
  text-align: center;
  font-size: 350%;
  font-family: "Amatic SC", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.headerlink {
  text-decoration: none;
  color: black;
}

.header::after, .header::before {
  content: "";
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  height: 10px;
  background-color: #6B6565;
}

@media screen and (max-width: 1100px) {
  .navigation, .navigationgallery, .navigation__link_beige, .navigationlist {
    margin-bottom: 5%;
  }
}
.navigation {
  background-color: #DCC6C6;
  width: 90%;
}

.navigation__link_white {
  color: #F0F0F0;
  float: right;
  padding: 20px 30px;
}

.navigationlist a {
  text-align: center;
  text-decoration: none;
  color: black;
  display: block;
  margin: 10px;
}
.navigationlist a:hover {
  font-size: 150%;
}

.button1,
.button2 {
  display: inline-block;
  padding: 10px 60px;
  margin: 0 10px;
  background-color: #F0F0F0;
  border-radius: 15px;
  text-decoration: none;
  text-align: center;
  color: black;
  font-family: "Amatic SC", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 1.5em;
}

@media screen and (min-width: 1100px) {
  .button1,
  .button2 {
    border-radius: 15px 15px 0 0;
  }
}
@media screen and (max-width: 1100px) {
  .button1,
  .button2 {
    width: 90%;
    font-size: 20px;
    margin: 10px;
  }
  .button1:focus,
  .button2:focus {
    background-color: transparent;
    color: #6B6565;
    border: #6B6565 2px solid;
  }
}
.navigation__link_beige {
  color: #6B6565;
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.galleryplaces_article img {
  padding: 10px;
  border: 2px black solid;
  width: 70%;
}

@media screen and (min-width: 1100px) {
  .article {
    display: flex;
    flex-direction: column;
  }
  .galleryplaces_article img {
    width: 40%;
    flex-direction: row;
  }
}
@media screen and (max-width: 1100px) {
  .galleryplaces {
    padding: 10px;
    border: 2px black dashed;
  }
}
@media screen and (min-width: 1100px) {
  .gallery img {
    width: 33.3333333333%;
    margin: 15px;
    padding: 15px;
    border: 2px black dashed;
  }
}
.galleryplaces1:hover, .galleryplaces2:hover, .galleryplaces3:hover,
.galleryplaces4:hover {
  opacity: 70%;
  border: #1d1b1b 10px solid;
}

@media screen and (max-width: 1100px) {
  .gallery img {
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
  }
}
h1 {
  margin: 5% 2%;
}

h1::after {
  content: "";
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  width: 50%;
  height: 3px;
  background-color: #6B6565;
}

.return {
  float: left;
  padding: 20px;
  margin: 20px;
}

/* SEKCE O MNĚ */
/* -------------------------- */
@media screen and (max-width: 1100px) {
  .aboutimage, .abouttext {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
}
.aboutimage img {
  border-radius: 40%;
  align-self: center;
  width: 40%;
}

.aboutimage, .abouttext {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  text-align: left;
  margin: 4%;
  padding: 4%;
}

/* -------------------------- */
.footer {
  padding: 40px;
  color: #F0F0F0;
  background-color: #6B6565;
  width: 100%;
  text-align: center;
}